.contact { width: 100%; padding-bottom: 95px; }
@media only screen and (max-width: 1200px) { .contact { padding-bottom: 0; } }
.contact .container { position: relative; }
.contact__map { background-size: cover; background-position: 50% 100%; background-repeat: no-repeat; padding-bottom: 348px; }
.contact h2 { margin-top: 72px; }
.contact h3 { font-size: 20px; line-height: 75px; padding-left: 10px; margin-top: 30px; }
@media only screen and (max-width: 500px) { .contact p { font-size: 16px; } }
@media only screen and (max-width: 400px) { .contact p { font-size: 16px; } }
.contact span { font-weight: 700; color: #8D8F92; }
.contact .button { width: 152px; padding: 0; line-height: 44px; background-color: #23B7A4; border: none; }
.contact .button:hover { line-height: 44px; padding: 0; }
.contact__text { max-width: 540px; }
.contact__form { max-width: 555px; top: -159px; right: -2px; position: absolute; background-color: #FFF; padding: 20px 30px 60px; }
@media only screen and (max-width: 1200px) { .contact__form { position: static; } }
@media only screen and (max-width: 500px) { .contact__form { padding-right: 0; max-width: 80%; } }
.contact__info { padding-bottom: 94px; }
@media only screen and (max-width: 600px) { .contact__info { padding-bottom: 5px; } }
.contact__name { width: 240px; float: left; height: 90px; display: inline-block; }
@media only screen and (max-width: 600px) { .contact__name { width: 110%; float: none; } }
@media only screen and (max-width: 500px) { .contact__name { width: 130%; } }
@media only screen and (max-width: 320px) { .contact__name { width: 133%; } }
.contact__email { width: 240px; float: right; height: 90px; display: inline-block; }
@media only screen and (max-width: 600px) { .contact__email { width: 110%; float: none; } }
@media only screen and (max-width: 500px) { .contact__email { width: 130%; } }
@media only screen and (max-width: 320px) { .contact__email { width: 133%; } }
@media only screen and (max-width: 600px) { .contact__message { width: 115.5%; } }
@media only screen and (max-width: 500px) { .contact__message { width: 136.5%; } }
@media only screen and (max-width: 320px) { .contact__message { width: 140%; } }
.contact__form-tittle { font-size: 14px; line-height: 26px; font-family: "Quattrocento Sans", sans-serif; font-weight: 700; text-transform: uppercase; color: #ABADAF; padding-left: 5px; }

.footer { width: 100%; background-color: #3A454D; line-height: 96px; }
@media only screen and (max-width: 600px) { .footer { line-height: 50px; } }
.footer__copyright { color: #CACDD0; font-size: 14px; line-height: 96px; float: left; }
@media only screen and (max-width: 600px) { .footer__copyright { float: none; } }
@media only screen and (max-width: 600px) { .footer__copyright { padding-top: 15px; line-height: 30px; } }
.footer__social { float: right; }
@media only screen and (max-width: 600px) { .footer__social { float: none; } }
.footer .footer__tw { background-image: url("../img/icons.png"); width: 155px; height: 18px; background-position: 0px 0px; display: inline-block; }


